* {
	margin: 0;
}
body {
	background-color: #f5f6fa;
}
.app {
	display: flex;
	justify-content: space-evenly;
	padding: 20px;
}
.app__header {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin-bottom: 20px;
}
.app__header > h1 {
	color: red;
	font-size: 2rem;
}
.app__dropdown {
	background-color: white;
}
.app__left {
	flex: 0.9;
}
.app__stats {
	display: flex;
	justify-content: space-between;
}
.app__right {
	display: flex;
	flex-direction: column;
}
.app__right .MuiCardContent-root {
	display: flex;
	flex-direction: column;
}
.app__graph {
	flex-grow: 1;
}
.app__graphTitle {
	margin-top: 20px;
	margin-bottom: 20px;
}
@media (max-width: 990px) {
	.app {
		flex-direction: column;
	}
}
